const bindEvents = () => {
  slider.addEventListener("click", () => {
    slider.classList.toggle("paused");
  });

  going.addEventListener("click", () => {
    base("Guest List").update(
      [
        {
          id: UserRecord?.id,
          fields: {
            Name: UserRecord?.Name,
            isGoing: true,
          },
        },
      ],
      function (err, records) {
        if (err) {
          console.error(err);
          return;
        }
        notgoing.classList.remove("active");
        going.classList.add("active");
        alert(
          "Thank you for your confirmation! If there are any changes to your plans prior to the event, please inform us. Thank you!"
        );
        // records.forEach(function (record) {
        //   notgoing.classList.remove("active");
        //   going.classList.add("active");

        // });
      }
    );
  });
  notgoing.addEventListener("click", () => {
    base("Guest List").update(
      [
        {
          id: UserRecord?.id,
          fields: {
            Name: UserRecord?.Name,
            isGoing: false,
          },
        },
      ],
      function (err, records) {
        if (err) {
          console.error(err);
          return;
        }
        going.classList.remove("active");
        notgoing.classList.add("active");
        alert(
          "Thank you for letting us know! If there are any changes to your plans prior to the event, please inform us. Thank you!"
        );
      }
    );
  });
};

// Preload images and fonts
preloadFonts("cvn8slu").then(() => {
  base("Guest List").find(uid, (err, record) => {
    if (err) {
      console.error(err);
      return;
    }
    UserRecord = { ...record.fields, id: record.id };
    console.log(UserRecord);
    username.innerHTML = UserRecord.Nickname;
    if (UserRecord?.showChristening) {
      const title = document.getElementById("christening-title");
      const content = document.getElementById("christening-content");
      title.classList.remove("hidden");
      content.classList.remove("hidden");
    }
    if (UserRecord?.isGoing) {
      going.classList.add("active");
    }
    if (UserRecord?.seatsReserved) {
      guestnote.innerHTML = `We have reserved <u>${UserRecord?.seatsReserved} seat(s)</u> for you. We kindly request that if there are any changes to your reservation, please inform us in advance, as the number of guests we can accommodate is limited.`;
    }
    const player = document.getElementById("bear-float");
    player.load("https://assets7.lottiefiles.com/packages/lf20_v8Ji51.json");
    player.addEventListener("load", () => {
      player.play();
    });
    Splitting();
    // Remove loader (loading class)
    document.body.classList.remove("loading");
    // Lenis (smooth scrolling)
    initSmoothScrolling();
    // GSAP Scroll Triggers
    scroll();
    bindEvents();
    // greetingNickname.innerHTML = `Hello, ${record.fields.Nickname}!`;
    // console.log("Retrieved", record, SplitText);
  });
});
